nav {
    background-color: #333;
    height: 50px;
    display: flex;
    align-items: center;
  }
  
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
  }
  
  li {
    margin: 0 10px;
  }
  
  a {
    color: #fff;
    text-decoration: none;
    font-size: 16px;
    font-weight: bold;
    padding: 10px;
    transition: all 0.2s;
  }
  
  a:hover {
    background-color: #555;
  }
  