* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  
  body {
    background-color : rgb(221, 220, 220);
    color: white;
    height: 100vh;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  }
  
  @mixin verticalAlign {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }