header {
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    h1 {
      color: black;
      letter-spacing: 2px;
      text-transform: uppercase;
      margin: 10px;
      &:hover {
        color: cyan;
      }
    }
    input[type="text"] {
      padding: 15px;
      border-radius: 10px;
      margin-bottom: 10px;
      border: 2px solid aqua;
    }
  }