.card {
    display: flex;
    flex-direction: column;
    width: 300px;
    margin: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  
    .img-container {
      position: relative;
      width: 100%;
      height: 0;
      padding-bottom: 56.25%;
      
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  
    .info-container {
      padding: 20px;
  
      h2 {
        margin-top: 0;
        margin-bottom: 10px;
        font-size: 24px;
      }
  
      span {
        display: block;
        margin-bottom: 5px;
        font-size: 16px;
        color: #666;
      }
  
      .genre {
        margin-top: 10px;
        font-size: 14px;
        color: #999;
        
        span:not(:last-child)::after {
          content: ", ";
        }
      }
  
      .overview {
        margin-top: 10px;
        font-size: 16px;
        line-height: 1.5;
        color: #333;
      }
    }
  }
  
